import React, {useEffect, useState} from "react";

export const GiveThingsFormInformation = (props) => {
	const {step} = props
	const [currentInfo,setCurrentInfo] = useState(step)

	useEffect(()=> {
		setCurrentInfo(step)
	},[step])

	return(
		<div className='things-form__information'>
			<h2 className='things-form__information__title'>Important !</h2>
			{currentInfo === 1 &&
			<p className='things-form__information__description'>
				Uzupenij szczegy dotyczce Twoich rzeczy. Dziki temu bdziemy wiedzie komu najlepiej je przekaza.
			</p>}
			{currentInfo === 2 &&
			<p className='things-form__information__description'>
				Wszystkie rzeczy do oddania zapakuj w 60l worki. Dokadn instrukcj jak poprawnie spakowa rzeczy znajdziesz TUTAJ.
			</p>}
			{currentInfo === 3 &&
			<p className='things-form__information__description'>
				Jeli wiesz komu chcesz pomc, moesz wpisa nazw tej organizacji w wyszukiwarce. Moesz te filtrowa organizacje po ich lokalizacji bd celu ich pomocy.
			</p>}
			{currentInfo === 4 &&
			<p className='things-form__information__description'>
				Podaj adres oraz termin odbioru rzeczy.
			</p>}
		</div>
	)

}